import { ErrorOutline } from "@mui/icons-material";
import { Alert, Box, CircularProgress, LinearProgress } from "@mui/material";
import { grey } from "@mui/material/colors";
import Grid from "@mui/material/Unstable_Grid2";
import React, { useState } from "react";

const TaskDrawerContent = ({
    task,
    axios,
    theme,
    friendLength,
    setSnackBar,
    tasks,
    setTasks,
    setUpgradeDrawer,
    setBalance,
    balance,
}) => {
    const [loading, setLoading] = useState(false);
    const mapped_tasks = (
        <Grid
            xs={12}
            marginTop={3.5}
            display={"flex"}
            justifyContent={"center"}
        >
            <Grid
                container="true"
                rowGap={1}
                columnGap={0.5}
                maxWidth={"100%"}
                display={"flex"}
                justifyContent={"center"}
                alignItems={"center"}
            >
                {task.prize.map((item, index) => (
                    <Grid
                        xs={3}
                        key={index}
                        // className={
                        // task.level - 1 < index
                        // ? task.level == index && "btn white blink"
                        // : "btn grey"
                        // : `btn white`
                        // }
                        width={"20%"}
                        padding={0.4}
                        className={task.level == index ? "magic-blink" : ""}
                        sx={{
                            backgroundImage:
                                "url(/assets/images/covers/purple-1.png) !important",
                            backgroundRepeat: "no-repeat !important",
                            backgroundPosition: "center center !important",
                            backgroundSize: "4.6rem !important",
                            backgroundOrigin: "padding-box !important",
                            imageRendering: "pixelated !important",
                            filter:
                                task.level - 1 < index
                                    ? "grayscale(100%)"
                                    : "none",
                            WebkitFilter:
                                task.level - 1 < index
                                    ? "grayscale(100%)"
                                    : "none",
                        }}
                    >
                        <Box
                            sx={{
                                backgroundImage:
                                    "url(/assets/particles/purple-cloud.gif)",
                                backgroundRepeat: "no-repeat",
                                backgroundPosition:
                                    task.level - 1 < index
                                        ? "50% 85%"
                                        : "51% 100%",
                                backgroundSize:
                                    task.level - 1 < index ? "10rem" : "12rem",
                                backgroundOrigin: "padding-box",
                                imageRendering: "pixelated",
                                width: "100%",
                                height: "100%",
                                zIndex: -1,
                            }}
                        >
                            <Box
                                display={"flex"}
                                justifyContent={"center"}
                                padding={1}
                                sx={{}}
                            >
                                <img
                                    height={"40px"}
                                    src="/assets/images/logo1.png"
                                    alt=""
                                    style={{
                                        marginTop: "-5px",
                                    }}
                                />
                            </Box>
                            <div
                                style={{
                                    marginTop: "-.6rem",
                                    fontFamily: "mortal-kombat",
                                    textAlign: "center",
                                    letterSpacing: "2px",
                                    textDecoration:
                                        task.level - 1 < index
                                            ? "unset"
                                            : "line-through",
                                    color:
                                        task.level - 1 < index
                                            ? "white"
                                            : grey[200],
                                }}
                            >
                                {item > 1000
                                    ? format(item)
                                    : item.toLocaleString()}
                            </div>
                        </Box>
                    </Grid>
                ))}
            </Grid>
        </Grid>
    );

    return (
        <div
            style={{
                padding: 4,
            }}
        >
            <div hidden={true}>{mapped_tasks}</div>
            <Grid marginTop={2} container="true">
                <Grid
                    xs={12}
                    display={"flex"}
                    justifyContent={"center"}
                    alignItems={"center"}
                >
                    <img
                        height={"70px"}
                        src="/assets/images/logo1.png"
                        alt=""
                    />
                    <span style={{ fontSize: "35px" }}>
                        {task.prize.length == 1
                            ? task.prize[0]
                            : task.prize[task.level]}
                    </span>
                </Grid>
                {task.prize.length > 1 ? mapped_tasks : ""}
            </Grid>
            <br />
            <div
                style={{
                    letterSpacing: "1px",
                }}
            >
                {task.description}
                {task.type == 3 ? (
                    <div>
                        <center>
                            <LinearProgress
                                value={
                                    (friendLength / task.data.minimum) * 100 >=
                                    100
                                        ? 100
                                        : (friendLength / task.data.minimum) *
                                          100
                                }
                                color={
                                    (friendLength / task.data.minimum) * 100
                                        ? "warning"
                                        : "success"
                                }
                                variant="determinate"
                                style={{
                                    maxWidth: "20rem",
                                }}
                            />
                        </center>
                        {/* <span
                            style={{
                                fontSize: "1.5rem",
                            }}
                        >{`${friendLength} / ${task.data.minimum}`}</span> */}
                    </div>
                ) : (
                    ""
                )}
                <br />
                {task.type != 2 && (
                    <Alert
                        style={{ textAlign: "start" }}
                        variant="outlined"
                        color="error"
                        icon={<ErrorOutline />}
                    >
                        <span
                            style={{
                                color: "white",
                                fontFamily: "sans-serif",
                                fontSize: "14px",
                            }}
                        >
                            <b>Attention</b>: Our system diligently reviews your
                            claim history. Please refrain from making false
                            claims; doing so will result in a <b>twofold</b>{" "}
                            reduction of your account balance.
                        </span>
                    </Alert>
                )}
            </div>
            <br />
            <button
                className={
                    loading
                        ? "btn white blink"
                        : task.claimed
                        ? "btn grey"
                        : `btn ${theme}`
                }
                onClick={() => {
                    setLoading(true);
                    axios
                        .post("/task/submit", {
                            id: task.id,
                        })
                        .then(({ data }) => {
                            const index = tasks.findIndex(
                                (item) => item.id == task.id
                            );
                            setBalance(
                                balance +
                                    task.prize[task.type != 2 ? 0 : task.level]
                            );
                            tasks[index].claimed = true;
                            const newTasks = [...tasks];
                            if (tasks[index].level >= 0) {
                                newTasks[index].level += 1;
                                setTasks(newTasks);
                            }
                            setSnackBar(["Task completed.", "success", true]);
                            setUpgradeDrawer([false]);
                        })
                        .catch((error) => {
                            console.log(error);
                            if (error.response?.reset) {
                                const index = tasks.findIndex(
                                    (item) => item.id == task.id
                                );
                                const newTasks = [...tasks];
                                newTasks[index].level = 0;
                                setTasks(newTasks);
                            }
                            if (
                                error.response?.status == 409 &&
                                error.response?.data?.status
                            ) {
                                const index = tasks.findIndex(
                                    (item) => item.id == task.id
                                );
                                const newTasks = [...tasks];
                                newTasks[index].claimed = true;
                                setTasks(newTasks);
                            }
                            setSnackBar([
                                error.response?.data?.message ||
                                    "Error fetching data. Plese check your connection.",
                                "error",
                                true,
                            ]);
                        })
                        .finally(() => {
                            setLoading(false);
                        });
                }}
                style={{
                    padding: "10px",
                    color: "white",
                    fontSize: "30px",
                    fontFamily: "mortal-kombat",
                    marginBottom: "7px",
                    textAlign: "center",
                }}
                disabled={
                    loading ||
                    task.claimed ||
                    (task.type == 3 && task.data.minimum > friendLength)
                }
            >
                <div
                    style={{
                        display: "flex",
                        alignItems: "center",
                    }}
                >
                    {loading ? <CircularProgress /> : ""}
                    <span
                        style={
                            loading
                                ? {
                                      marginLeft: "8px",
                                  }
                                : {}
                        }
                    >
                        {loading
                            ? "Fetching data"
                            : task.type == 3 && task.data.minimum > friendLength
                            ? `${friendLength} / ${task.data.minimum}`
                            : task.claimed
                            ? "Claimed"
                            : "Claim"}
                    </span>
                </div>
            </button>
        </div>
    );

    function format(number) {
        const formatter = new Intl.NumberFormat("en", {
            notation: "compact",
            compactDisplay: "short",
            maximumSignificantDigits: 3,
        });
        return formatter.format(number);
    }
};

export default TaskDrawerContent;
