import Grid from "@mui/material/Unstable_Grid2";
import React from "react";
import TaskDrawerContent from "./taskDrawerContent";
import { grey } from "@mui/material/colors";

const TaskCard = ({
    task,
    setUpgradeDrawer,
    theme,
    axios,
    friendLength,
    setSnackBar,
    tasks,
    setTasks,
    setBalance,
    balance,
    colors,
}) => {
    const telegram = window.Telegram.WebApp;
    return (
        <Grid
            xs={12}
            sx={{
                backdropFilter: "blur(4.5px)",
                WebkitBackdropFilter: "blur(4.5px)",
            }}
        >
            <Grid
                container="true"
                sx={{
                    borderRadius: "10px",
                    background: "rgba(50, 50, 50, 0.20) !important",
                    boxShadow: "0 4px 30px rgba( 25, 25, 25, 0.8) !important",
                    // border:
                    //     "1px solid " +
                    //     (task.claimed ? "grey" : `rgb(${colors[theme]})`),
                    height: "100%",
                    padding: ".5rem .1rem .5rem .1rem",
                    width: "100%",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                }}
                className={
                    task.upcoming
                        ? "btn white blink"
                        : task.claimed
                        ? "btn grey"
                        : `btn ${theme}`
                }
            >
                <Grid
                    xs={3}
                    sx={{
                        display: "flex",
                        alignItems: "center",
                    }}
                >
                    <img
                        height={"55rem"}
                        style={{
                            imageRendering: task.pixelated
                                ? "pixelated"
                                : "auto",
                        }}
                        src={task.image}
                        alt=""
                    />
                </Grid>
                <Grid xs={5.7}>
                    <span
                        style={{
                            textAlign: "center",
                            fontSize: "14px",
                            letterSpacing: "1px",
                            textDecoration: task.claimed
                                ? "line-through"
                                : "unset",
                            color: task.claimed ? grey[400] : "white",
                        }}
                    >
                        {task.title}
                    </span>
                </Grid>
                <Grid xs={3} display={"flex"} justifyContent={"center"}>
                    <button
                        className={
                            task.upcoming
                                ? "btn white blink"
                                : task.claimed
                                ? "btn grey"
                                : "btn " + theme
                        }
                        disabled={
                            (task.claimed || task.upcoming) && task.type != 2
                        }
                        onClick={() => {
                            switch (task.type) {
                                case 0:
                                    telegram.openTelegramLink(task.data.url);
                                    break;
                                case 1:
                                    telegram.openLink(task.data.url);
                                    break;
                            }
                            setUpgradeDrawer([
                                true,
                                <TaskDrawerContent
                                    setBalance={setBalance}
                                    balance={balance}
                                    setUpgradeDrawer={setUpgradeDrawer}
                                    theme={theme}
                                    setSnackBar={setSnackBar}
                                    task={task}
                                    friendLength={friendLength}
                                    axios={axios}
                                    tasks={tasks}
                                    setTasks={setTasks}
                                />,
                            ]);
                        }}
                        style={{
                            padding: "7px 10px 7px 10px",
                            color: "white",
                            fontWeight: 600,
                            fontFamily: "sans-serif",
                            letterSpacing: "1px",
                            fontSize: "1.05rem",
                        }}
                    >
                        {task.upcoming
                            ? "Soon..."
                            : task.claimed
                            ? "âœ…"
                            : task.type == 3
                            ? friendLength >= task.data.minimum
                                ? "Claim"
                                : `${friendLength} / ${task.data.minimum}`
                            : task.type == 2
                            ? "Claim"
                            : "Start"}
                    </button>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default TaskCard;
